<template>
  <div class="page page_videos bg_white">
    <section class="page-content">
      <div class="table">
        <div class="table-cell">
          <div class="row">
            <div class="col-lg-8 ">
              <iframe class="video-frame video_full-height" frameborder="0"
                      :src="page_videos['videos'][0] | frame">
              </iframe>
            </div>
            <div class="col-lg-4">
              <div class="row">
                <div class="col-md-12">
                  <iframe class="video-frame video_half-height" frameborder="0"
                          :src="page_videos['videos'][1] | frame">
                  </iframe>
                </div>
                <div class="col-md-12">
                  <iframe class="video-frame video_half-height" frameborder="0"
                          :src="page_videos['videos'][2] | frame">
                  </iframe>
                </div>
              </div>


            </div>
          </div>
        </div>
      </div>


    </section>
    <!--<ul class="videos">-->
    <!--<li v-for="video in page_videos.videos">-->
    <!--<iframe :src="video | frame" frameborder="0" allowfullscreen></iframe>-->
    <!--</li>-->
    <!--</ul>-->
    <button class="button__back" @click="navigateBack">{{page_videos[$route.params.lang]['buttons']['back']}}</button>
    <button class="button__scroll-down button__scroll-down__white" @click="scrollDown">{{page_videos[$route.params.lang]['buttons']['scroll']}}</button>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        page_videos: {
          ru: {
            buttons: {
              back: 'Назад',
              scroll: 'Листать вниз'
            }
          },
          en: {
            buttons: {
              back: 'Back',
              scroll: 'Scroll Down'
            }
          },
          videos: [
            'YBHbri743qs',
            'p929fy7bgJs',
            'juvEXnyVRlM'
          ]
        }
      }
    },
    methods: {
      navigateBack(){
        this.$route.push(window.history.back());
      },
      scrollDown(){

      }
    },
    filters: {
      frame(video_id){
        return 'https://www.youtube.com/embed/' + video_id + '?enablejsapi=1&showinfo=0&color=white&autohide=2&showinfo=0&rel=0';
      }
    },
  }
</script>

<style lang="sass" src="./../assets/style/page_video.sass"></style>



